import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { defaultDataState, DataListState } from './types/index';
import { BaseDataFacadeService } from '@farris/ui-common';

@Injectable()
export class DataListFacadeService extends BaseDataFacadeService<DataListState> {
    containerHeight$ = this.state$.pipe(
        map((state: DataListState) => {
            return state.containerHeight;
        })
    );

    options$ = this.state$;

    constructor() {
        super(defaultDataState);
    }

    updateContainerSize(width: number, height: number) {
        // tslint:disable-next-line:variable-name
        let _height = height;
        if (this._state.showHeader) {
            _height = height - this._state.headerHeight;
        }

        this.updateState({ containerHeight: _height, width, height });
    }
}
